.header-logo__logo {
    width: 86px;
    height: 35px;
    margin-top: 22px;
    margin-bottom: 22px;
}

.header-logo__toggle {
    position: absolute;
    width: 21px;
    height: 21px;
    border: 0;
    top: 29px;
    right: 0;
    background-color: transparent;
    background-image: url("../../img/menu-close.png");
}

@media (min-width: 768px) {
    .header-logo__toggle {
      display: none;
    }
  }

